.container {
  display: flex;
  align-items: center;
}

.name {
  font-weight: 700;
  margin-right: 12px;
  color: #fff;
}

.button {
  border: transparent;
  border-radius: 5%;
  color: #fff;
  background-color: #e84a5f;
  &:hover,
  &:focus {
    background-color: #b63a4a;
  }
}
